<template>
  <div id="app" class="wrapper">
    <div class="logo">
      <img src="./assets/logo.png">
    </div>
    <div class="wrapper-container">
      <div class="wrapper-navigate">
        <Navigator />
      </div>
      <transition name="fade" mode="out-in">
        <router-view class="view wrapper-content"></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
import Navigator from './components/Navigator.vue'

export default {
  name: 'app',
  components: {
    Navigator
  }
}
</script>

<style lang="stylus">
@import './style/common.styl'

.wrapper {
  background #eee
  padding 1px
}
.logo {
  margin 20px auto
  height 80px
  text-align center
  img {
    width 80px
  }
}
.wrapper-container {
  background #fff
  width 90%
  margin 30px auto 20px
  border-radius 6px
  box-shadow 0 1px 1px 0 rgba(0,0,0,.1)
  overflow hidden
}
.wrapper-navigate {
  float left
  flex 0 0 auto
  display block
  width 16.66666667%
}

.wrapper-content {
  float left
  flex 0 0 auto
  display block
  width 83.33333333%
  padding 20px 25px
}

</style>



